/**
 * makeExpandingArea
 * outset inset 两种模式
 * @param {HtmlElement} textarea
 * @param {string} type
 * @access private
 */

function makeExpandingArea(textarea, type) {
  var isRender = textarea.dataset.render;
  if (isRender) return false;
  var self = this;
  var name = textarea.dataset.ctformConfigName;
  var container = createContainer(textarea, type, function (parent) {
    var dom = parent.querySelector('textarea');
    dom.parentNode.replaceChild(textarea.cloneNode(true), dom);
  });
  var textAreaDom = container.querySelector('textarea');
  var spanDom = container.querySelector('span');
  var rangetipDom = container.querySelector('.rangetip');
  var maxLangth;

  if (rangetipDom) {
    maxLangth = window.parseInt(textAreaDom.dataset.ctformConfigMaxlength);
    textAreaDom.setAttribute('maxlength', "".concat(maxLangth));
  }

  var ctFormExpandingareaInner = container.querySelector('.ct-form-expandingarea-inner');
  textAreaDom.dataset.render = 'true';
  textAreaDom.addEventListener('input', function () {
    spanDom.textContent = textAreaDom.value;

    if (rangetipDom) {
      if (this.value.length <= maxLangth) {
        rangetipDom.innerText = rangeTipTemplate({
          minLength: this.value.length,
          maxLength: maxLangth
        });
      }
    }
  }, false);
  textAreaDom.addEventListener('focus', function (e) {
    if (!e.relatedTarget) {
      self.hideErrorMessageTip(name);
    }

    if (textarea.dataset.ctformConfigInset) {
      if (!ctFormExpandingareaInner.classList.contains('focus')) {
        ctFormExpandingareaInner.classList.add('focus');
      }
    }
  });
  textAreaDom.addEventListener('blur', function () {
    if (textarea.dataset.ctformConfigInset) {
      if (ctFormExpandingareaInner.classList.contains('focus')) {
        ctFormExpandingareaInner.classList.remove('focus');
      }
    }
  });
  spanDom.textContent = textAreaDom.value;
  container.querySelector('.ct-form-expandingarea').className += ' active';
}
