/**
 * makeSpinner
 * @param {HtmlElement} spinner
 * @param {string} type
 * @access private
 */


function makeSpinners(spinner, type) {
  var isRender = spinner.dataset.render;
  if (isRender) return false;
  var self = this;
  var name = spinner.dataset.ctformConfigName;
  var container = createContainer(spinner, type, function (parent) {
    var dom = parent.querySelector('input[type="number"].ios');
    dom.parentNode.replaceChild(spinner.cloneNode(true), dom);
  });
  var min = window.parseInt(spinner.getAttribute('min')) || 1;
  var max = window.parseInt(spinner.getAttribute('max')) || 100;
  var setp = window.parseInt(spinner.getAttribute('setp')) || 1;
  var value = window.parseInt(spinner.value) || 0;
  container.querySelector('.ct-form-spinner').className += spinner.className;
  var spinnerDom = container.querySelector('input[type="number"].ios');
  var plusDom = container.querySelector('.plus');
  var minusDom = container.querySelector('.minus');
  spinnerDom.dataset.render = 'true'; // plus

  plusDom.addEventListener('click', function () {
    self.hideErrorMessageTip(name);
    if (spinnerDom.disabled || spinnerDom.readOnly) return false;
    if (value >= max) return false;

    if (value + setp > max) {
      value += value + setp - max;
    } else {
      value += setp;
    }

    spinnerDom.value = value;
  }); // minus

  minusDom.addEventListener('click', function () {
    self.hideErrorMessageTip(name);
    if (spinnerDom.disabled || spinnerDom.readOnly) return false;
    if (value <= min) return false;

    if (value - setp < min) {
      value -= setp - min;
    } else {
      value -= setp;
    }

    spinnerDom.value = value;
  });
}
